:root {
  --primary: #2ecc71;
}

.btn {
  padding: 8px 20px;
  border-radius: 4px;
  outline: none;
  border: none;
  cursor: pointer;
  font-weight: bold;
}

.btn:hover {
  padding: 8px 20px;
  transition: all 0.3s ease-out;
  background: black;
  color: #fff;
  transition: 250ms;
}

.btn--primary {
  background-color: var(--primary);
}

.btn--outline {
  background-color: transparent;
  color: #fff;
  padding: 8px 20px;
  border-radius: 4px;
  border: 1px solid var(--primary);
  transition: all 0.3s ease-out;
}

.btn--medium {
  padding: 10px 20px;
  border-radius: 4px;
  font-size: 20px;
  color: #fff;
  margin-right: 15px;
}

.btn-large {
  padding: 12px 26px;
  border-radius: 4px;
  font-size: 20px;
  color: #fff;
}
